// 
import vueSimpleTable from "./vue-simple-table/index.js";

// 
const components = [vueSimpleTable];
//  install  Vue  use 
const install = function(Vue) {
  // 
  if (install.installed) return;
  // 
  components.forEach(component => {
    Vue.component(component.name, component)
  });
};
// 
if (typeof window !== "undefined" && window.Vue) {
  install(window.Vue);
}
export default {
  //  install Vue.use() 
  install,
  // 
  vueSimpleTable
};